}
}
@media(max-width:800px){
.main {
    width: 51%;
}
.w3 {
    float: none;
    width: 100%;
    margin-right: 0;
    margin-bottom: 5%;
}
.agile {
    float: none;
    width: 100%;
}
.signin-form p a {
    line-height: 1.8em;
}
}
@media(max-width:768px){
.main {
    width: 53%;
}
}
@media(max-width:736px){
.main {
    width: 55%;
}
}
/*-- w3layouts --*/
@media(max-width:667px){
.main {
    width: 61%;
}
}
@media(max-width:640px){
.main {
    width: 64%;
}
}
@media(max-width:600px){
.main {
    width: 68%;
}
}
@media(max-width:568px){
.main {
    width: 72%;
}
}
@media(max-width:480px){
