    width: 78%;
}
.header-social a.face {
    width: 27%;
}
.header-social a.goog {
    width: 21%;
}
.w3 {
    margin-bottom: 0%;
}
.signin-form input[type="text"], .signin-form input[type="password"] {
    width: 91%;
}
h1 {
    font-size: 2.2em;
}
.profile {
    margin-top: 2em;
}
.signin-form {
    padding: 2em 2em;
}
.copyright p {
    font-size: 0.9em;
}
/*-- agileits --*/
}
@media(max-width:414px){
.signin-form input[type="text"], .signin-form input[type="password"] {
    width: 89%;
}
.signin-form {
    padding: 2em 1.5em;
}
.header-social a.face {
    width: 25%;
}
.header-social a.goog {
    width: 19%;
}
.tp {
    margin: 1.7em 0 0;
}
.header-social {
    margin-bottom: 1em;
}
.signin-form h3 {
    margin-bottom: 1em;
}
.login-form {
    margin: 1em 0 1.5em;
}
}
@media(max-width:384px){
h1 {
    font-size: 2em;
}
.header-social a.face {
    width: 22%;
}
.header-social a.goog {
    width: 17%;
}
}
@media(max-width:375px){
.main {
    width: 79%;
}
.header-social a.goog {
